.contact-container {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-content: center;

	margin: 20px auto 20px auto;
}

.contact-entry {
	display: flex;
	flex-grow: 1;

	margin: 10px;
}

.contact-icon {
	width: 40px;
}

.contact-text {
	padding: 10px;
	font-size: 20px;
	margin-left: 10px;
	margin-top: 0;
}

/***** Media Queries *****/
@media (max-width: 480px) {
	.contact-icon {
		width: 32px;
	}

	.contact-text {
		padding: 10px;
		font-size: 16px;
		margin-left: 8px;
		margin-top: 0;
	}
}
