@import url(https://fonts.googleapis.com/css?family=Fjalla+One);
@import url(https://fonts.googleapis.com/css?family=EB+Garamond);
body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;

  background-color: #E0E0E0;
}

html, body {
  height: 100%;
  overflow: hidden;
}

#root {
  height: 100%;
}

#main-logo-container {
  display: block;
  overflow: hidden;

  margin-left: 20px;
  margin-right: 55px;
}

#main-logo {
  font-family: 'Fjalla One', sans-serif;
  color: white;

  font-size: 30px;
  text-decoration: none;
}

#navigation-bar {
  background-color: black;
}

.navigation-tabs {
  display: block;
  overflow: hidden;
}

.mobile.navigation-tabs {
  display: none;
}

#social-buttons {
  display: block;
  overflow: hidden;

  position: absolute;
  right: 0;

  margin-right: 30px;
}

#social-buttons > a {
  margin: 0 5px;
}

.social-icon {
  width: 30px;
}

@media screen and (max-width: 800px) {
  #main-logo-container {
    margin-left: 15px;
    margin-right: 35px;
  }

  #social-buttons {
    margin-right: 15px;
  }
}

@media (max-width: 600px) {
  #main-logo-container {
    margin-left: 10px;
    margin-right: 25px;
  }

  #social-buttons {
    margin-right: 10px;
  }
  
  #social-buttons > a {
    margin: 0;
  }

  .social-icon {
    width: 25px;
  }
}

@media (max-width: 540px) {
  .social-icon {
    width: 30px;
  }

  .mobile.navigation-tabs {
    display: block;
  }

  .desktop.navigation-tabs {
    display: none;
  }
}

@media (max-width: 300px) {
  #social-buttons {
    display: none;
  }

  #main-logo-container {
    margin: auto;
  }
}

.banner-container {
  left: 0;
  right: 0;

  position: relative;
  height: 300px;

  overflow: hidden;
  /*transform-style: preserve-3d;*/
}

.banner-background {
  height: 100%;
  width: 100%;

  /* Background Image */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;

  /* Parallax Scroll */
  /*z-index: -100;
  transform: translateZ(-500px) scale(2.1);*/
}

.banner-overlay {
  display: block;
  position: absolute;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  padding: 0 60px;

  background: rgba(0, 0, 0, 0.7);
}

.banner-text {
  display: block;
  position: relative;

  color: white;
  text-align: center;

  top: 5%;
}

.banner-text > h1 {
  font-family: 'Fjalla One', sans-serif;
  font-size: 64px;
}

.banner-text > p {
  font-size: 28px;
}

.down-arrow {
  width: 50px;

  position: absolute;
  display: none;

  left: 0;
  right: 0;
  margin: auto;
  bottom: 20px;
}


/* Media Queries */

@media (max-width: 850px), (max-height: 400px) {
  .banner-container {
    height: 200px;
  }

  .banner-text > h1 {
    font-size: 48px;
  }

  .banner-text > p {
    font-size: 20px;
  }
}

@media (max-height: 600px) and (min-width: 481px) {
  .banner-container {
    height: 100%;
  }
  
  .banner-text {
    top: calc(30% - 75px);
  }

  .down-arrow {
    display: block;
  }
}

@media (max-width: 600px) {
	.banner-text > p {
		font-size: 18px;
	}
}

@media (max-width: 480px) {
  .banner-container {
    height: 96px;
  }

  .banner-text {
    top: 0;
  }

  .banner-text > h1 {
    font-size: 36px;
    margin-bottom: 0px;
  }

  .banner-text > p {
    display: none;
  }
}

#page-base {
  height: 100%;
}

#page-container {
  height: calc(100% - 64px);
  margin-top: 64px;

  overflow-x: hidden;

  -webkit-perspective: 500px;

          perspective: 500px;
}

#content-container {
  background: #E0E0E0;
  z-index: 1000;
  position: relative;

  padding: 40px;
  width: calc(100% - 80px);
}

.text-container {
  left: 0;
  right: 0;

  width: 600px;
  margin: 0 calc(50% - 300px);
}

.text-container > p {
  text-align: justify;

  margin-top: 0;
  font-family: 'EB Garamond', serif;
  font-size: 22px;
}


/* Media queries */

@media (max-width: 1000px) {
  #content-container {
    padding: 30px;
    width: calc(100% - 60px);
  }
}

@media (max-width: 700px) {
  .text-container {
    width: calc(100% - 40px);
    margin: 0 20px;
  }
}

@media (max-width: 600px) {
  #page-container {
    margin-top: 48px;
    height: calc(100% - 48px);
  }
}

@media (max-width: 540px) {
  #page-container {
    margin-top: 104px;
    height: calc(100% - 104px);
  }

  #content-container {
    padding: 15px;
    width: calc(100% - 30px);
  }

  .text-container {
    margin-top: 15px;
  }

  .text-container > p {
    text-align: left;
  }
}

@media (max-width: 540px) and (orientation: landscape) {
  #page-container {
    margin-top: 96px;
    height: calc(100% - 96px);
  }
}

#project-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

/***** Project Cards *****/
.project-card {
	width: calc(33.3% - 20px);

	margin: 10px;

	overflow: hidden;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	cursor: pointer;
}

.project-card:hover {
	-webkit-transform: scale(1.01);
	        transform: scale(1.01);
	box-shadow: 5px 5px 10px rgba(0,0,0,0.3);
}

.project-info-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.project-image {
	height: 200px;
}


/*** Header ***/
.project-header {
	display: -webkit-flex;
	display: flex;
	width: 100%;
}

.project-title {
	float: left;
	overflow-x: hidden;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.project-date {
	float: right;
	text-align: right;
	padding-top: 3px;
}

/*** Content ***/
.project-info {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

a {
	text-decoration: none;
	color: black;
}

/*** Footer ***/
.project-footer {
	margin-top: auto;
	display: -webkit-flex;
	display: flex;
}

/* Tags */
.project-tags-container {
	/* Fill all allotted space */
	display: -webkit-flex;
	display: flex;
	-webkit-flex-grow: 1;
	        flex-grow: 1;

	/* Positioning and element wrapping*/
	white-space: nowrap;
	margin-right: 10px;
	margin-top: 3px;

	/* Clip scrollbar in .project-tags */
	overflow: hidden;
	height: 32px;
}

.project-tag-label {
	padding-top: 9px;
}

.project-tags {
	display: inline-block;
	white-space: nowrap;
	overflow-x: auto;

	height: 60px; /* Push scrollbar out of view */
}

.project-tag {
	margin-left: 6px;
}

/* Social Icons */
.project-social {
	display: inline-block;
	float: right;
	white-space: nowrap;
}

.project-social a {
	padding: 4px;
}

.project-social a img {
	width: 32px;
}


/***** Media Queries *****/

@media (min-width: 2200px) {
	.project-card {
		width: calc(25% - 20px);
	}
}

@media (max-width: 1300px) {
	.project-card {
		width: calc(50% - 20px);
	}
}

@media (max-width: 850px) {
	.project-card {
		margin: 5px;
		width: calc(100% - 10px);
	}
}

@media (max-width: 480px) {
	.project-header {
		display: inline-block;
	}
	
	.project-title {
		width: 100%;
	}

	.project-date {
		float: left;
		text-align: left;
		margin-top: 2px;
	}

	.project-info p {
		margin-top: 10px;
	}
}

.tag-filter {
  margin: 10px;
  display: -webkit-flex;
  display: flex;
}

.tag-filter p {
  margin: auto 15px auto 10px;
  padding: 5px 0px 5px 0px;
  font-family: Roboto;
  font-size: 16px;
}

/* Media Queries */
@media (max-width: 850px) {
  .tag-filter {
    margin: 5px;
  }
}



.contact-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-align-content: center;
	        align-content: center;

	margin: 20px auto 20px auto;
}

.contact-entry {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-grow: 1;
	        flex-grow: 1;

	margin: 10px;
}

.contact-icon {
	width: 40px;
}

.contact-text {
	padding: 10px;
	font-size: 20px;
	margin-left: 10px;
	margin-top: 0;
}

/***** Media Queries *****/
@media (max-width: 480px) {
	.contact-icon {
		width: 32px;
	}

	.contact-text {
		padding: 10px;
		font-size: 16px;
		margin-left: 8px;
		margin-top: 0;
	}
}

