/***** Project Cards *****/
.project-card {
	width: calc(33.3% - 20px);

	margin: 10px;

	overflow: hidden;

	display: flex;
	flex-direction: column;
	cursor: pointer;
}

.project-card:hover {
	transform: scale(1.01);
	box-shadow: 5px 5px 10px rgba(0,0,0,0.3);
}

.project-info-container {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
}

.project-image {
	height: 200px;
}


/*** Header ***/
.project-header {
	display: flex;
	width: 100%;
}

.project-title {
	float: left;
	overflow-x: hidden;
	flex-grow: 1;
}

.project-date {
	float: right;
	text-align: right;
	padding-top: 3px;
}

/*** Content ***/
.project-info {
	flex-grow: 1;
}

a {
	text-decoration: none;
	color: black;
}

/*** Footer ***/
.project-footer {
	margin-top: auto;
	display: flex;
}

/* Tags */
.project-tags-container {
	/* Fill all allotted space */
	display: flex;
	flex-grow: 1;

	/* Positioning and element wrapping*/
	white-space: nowrap;
	margin-right: 10px;
	margin-top: 3px;

	/* Clip scrollbar in .project-tags */
	overflow: hidden;
	height: 32px;
}

.project-tag-label {
	padding-top: 9px;
}

.project-tags {
	display: inline-block;
	white-space: nowrap;
	overflow-x: auto;

	height: 60px; /* Push scrollbar out of view */
}

.project-tag {
	margin-left: 6px;
}

/* Social Icons */
.project-social {
	display: inline-block;
	float: right;
	white-space: nowrap;
}

.project-social a {
	padding: 4px;
}

.project-social a img {
	width: 32px;
}


/***** Media Queries *****/

@media (min-width: 2200px) {
	.project-card {
		width: calc(25% - 20px);
	}
}

@media (max-width: 1300px) {
	.project-card {
		width: calc(50% - 20px);
	}
}

@media (max-width: 850px) {
	.project-card {
		margin: 5px;
		width: calc(100% - 10px);
	}
}

@media (max-width: 480px) {
	.project-header {
		display: inline-block;
	}
	
	.project-title {
		width: 100%;
	}

	.project-date {
		float: left;
		text-align: left;
		margin-top: 2px;
	}

	.project-info p {
		margin-top: 10px;
	}
}
