@import url('https://fonts.googleapis.com/css?family=EB+Garamond');


#page-base {
  height: 100%;
}

#page-container {
  height: calc(100% - 64px);
  margin-top: 64px;

  overflow-x: hidden;

  perspective: 500px;
}

#content-container {
  background: #E0E0E0;
  z-index: 1000;
  position: relative;

  padding: 40px;
  width: calc(100% - 80px);
}

.text-container {
  left: 0;
  right: 0;

  width: 600px;
  margin: 0 calc(50% - 300px);
}

.text-container > p {
  text-align: justify;

  margin-top: 0;
  font-family: 'EB Garamond', serif;
  font-size: 22px;
}


/* Media queries */

@media (max-width: 1000px) {
  #content-container {
    padding: 30px;
    width: calc(100% - 60px);
  }
}

@media (max-width: 700px) {
  .text-container {
    width: calc(100% - 40px);
    margin: 0 20px;
  }
}

@media (max-width: 600px) {
  #page-container {
    margin-top: 48px;
    height: calc(100% - 48px);
  }
}

@media (max-width: 540px) {
  #page-container {
    margin-top: 104px;
    height: calc(100% - 104px);
  }

  #content-container {
    padding: 15px;
    width: calc(100% - 30px);
  }

  .text-container {
    margin-top: 15px;
  }

  .text-container > p {
    text-align: left;
  }
}

@media (max-width: 540px) and (orientation: landscape) {
  #page-container {
    margin-top: 96px;
    height: calc(100% - 96px);
  }
}
