@import url('https://fonts.googleapis.com/css?family=Fjalla+One');

#main-logo-container {
  display: block;
  overflow: hidden;

  margin-left: 20px;
  margin-right: 55px;
}

#main-logo {
  font-family: 'Fjalla One', sans-serif;
  color: white;

  font-size: 30px;
  text-decoration: none;
}

#navigation-bar {
  background-color: black;
}

.navigation-tabs {
  display: block;
  overflow: hidden;
}

.mobile.navigation-tabs {
  display: none;
}

#social-buttons {
  display: block;
  overflow: hidden;

  position: absolute;
  right: 0;

  margin-right: 30px;
}

#social-buttons > a {
  margin: 0 5px;
}

.social-icon {
  width: 30px;
}

@media screen and (max-width: 800px) {
  #main-logo-container {
    margin-left: 15px;
    margin-right: 35px;
  }

  #social-buttons {
    margin-right: 15px;
  }
}

@media (max-width: 600px) {
  #main-logo-container {
    margin-left: 10px;
    margin-right: 25px;
  }

  #social-buttons {
    margin-right: 10px;
  }
  
  #social-buttons > a {
    margin: 0;
  }

  .social-icon {
    width: 25px;
  }
}

@media (max-width: 540px) {
  .social-icon {
    width: 30px;
  }

  .mobile.navigation-tabs {
    display: block;
  }

  .desktop.navigation-tabs {
    display: none;
  }
}

@media (max-width: 300px) {
  #social-buttons {
    display: none;
  }

  #main-logo-container {
    margin: auto;
  }
}
