.banner-container {
  left: 0;
  right: 0;

  position: relative;
  height: 300px;

  overflow: hidden;
  /*transform-style: preserve-3d;*/
}

.banner-background {
  height: 100%;
  width: 100%;

  /* Background Image */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;

  /* Parallax Scroll */
  /*z-index: -100;
  transform: translateZ(-500px) scale(2.1);*/
}

.banner-overlay {
  display: block;
  position: absolute;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  padding: 0 60px;

  background: rgba(0, 0, 0, 0.7);
}

.banner-text {
  display: block;
  position: relative;

  color: white;
  text-align: center;

  top: 5%;
}

.banner-text > h1 {
  font-family: 'Fjalla One', sans-serif;
  font-size: 64px;
}

.banner-text > p {
  font-size: 28px;
}

.down-arrow {
  width: 50px;

  position: absolute;
  display: none;

  left: 0;
  right: 0;
  margin: auto;
  bottom: 20px;
}


/* Media Queries */

@media (max-width: 850px), (max-height: 400px) {
  .banner-container {
    height: 200px;
  }

  .banner-text > h1 {
    font-size: 48px;
  }

  .banner-text > p {
    font-size: 20px;
  }
}

@media (max-height: 600px) and (min-width: 481px) {
  .banner-container {
    height: 100%;
  }
  
  .banner-text {
    top: calc(30% - 75px);
  }

  .down-arrow {
    display: block;
  }
}

@media (max-width: 600px) {
	.banner-text > p {
		font-size: 18px;
	}
}

@media (max-width: 480px) {
  .banner-container {
    height: 96px;
  }

  .banner-text {
    top: 0;
  }

  .banner-text > h1 {
    font-size: 36px;
    margin-bottom: 0px;
  }

  .banner-text > p {
    display: none;
  }
}
